/* jshint ignore:start */

if (!runningTests) {
  require("testing/app")["default"].create({"name":"testing","version":"0.0.0+"});
}

define('~fastboot/app-factory', ['testing/app', 'testing/config/environment'], function(App, config) {
  App = App['default'];
  config = config['default'];

  return {
    'default': function() {
      return App.create(config.APP);
    }
  };
});


/* jshint ignore:end */
